import React from "react";

/**
 * Formats trademark symbols (®, ™, ©) in text to display smaller and as superscript.
 * Use this for visible display of names that may contain these symbols.
 */
export const formatTrademarkSymbols = (text: string | undefined | null): React.ReactNode => {
  if (!text) return text;
  
  const parts = text.split(/(®|™|©)/g);
  if (parts.length === 1) return text;
  
  return parts.map((part, i) =>
    ['®', '™', '©'].includes(part) 
      ? <span key={i} className="text-[0.45em] font-normal align-top leading-none relative top-[0.15em]">{part}</span>
      : part
  );
};

/**
 * Formats trademark symbols in HTML strings for use with dangerouslySetInnerHTML.
 * Wraps ®, ™, © with a styled span to display smaller.
 */
export const formatTrademarkSymbolsInHtml = (html: string | undefined | null): string => {
  if (!html) return html || '';
  return html.replace(/([®™©])/g, '<span style="font-size:0.45em;font-weight:normal;vertical-align:top;line-height:1;position:relative;top:0.15em">$1</span>');
};
